<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-employee-sanksi',
  data () {
    return {
      config: {
        title: 'Sanksi Tenaga Kerja',
        model_api: 'employee-sanksi',
        getter: 'employee-sanksi',
        setter: 'employee-sanksi',
        pk_field: null,
        permission: {
          create: 'create-employee-sanksi',
          read: 'view-employee-sanksi',
          update: 'update-employee-sanksi',
          delete: 'delete-employee-sanksi'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'date', label: 'Tanggal', methods: { list: { transform: 'longDate' }, detail: { transform: 'longDate' }, create: { type: 'date' }, update: { type: 'date' }, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { list: { show: false }, create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'sanksi_value', label: 'Nilai Sanksi', methods: { create: { type: 'money' }, update: { type: 'money' }, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'base_payment_value', label: 'Nilai Dasar Perbulan', methods: { create: { type: 'money' }, update: { type: 'money' }, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'remaining_sanksi_value', label: 'Sisa Nilai Sanksi', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'count_payment', label: 'Kali Pembayaran', methods: { create: false, update: false, list: { transform: 'suffix-kali' }, detail: { transform: 'suffix-kali' } } },
          {
            id: 'is_reduce_salary',
            label: 'Pengurangan Gaji?',
            methods: {
              list: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              detail: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              create: {
                type: 'radio',
                value: false,
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  },
                  toggle_field: [
                    { id: 'sanksi_value', item: 'show', toggle: [true, false], value: true },
                    { id: 'base_payment_value', item: 'show', toggle: [true, false], value: true }
                  ]
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              }
            }
          },
          {
            id: 'sanksi_type_id',
            label: 'Komponen Sanksi',
            methods: {
              create: {
                setter: 'master-sanksi-types',
                getter: 'master-sanksi-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name', 'rel_salary_component_id']
                  }
                }
              },
              update: {
                setter: 'master-sanksi-types',
                getter: 'master-sanksi-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name', 'rel_salary_component_id']
                  }
                }
              },
              filter: {
                setter: 'master-sanksi-types',
                getter: 'master-sanksi-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name', 'rel_salary_component_id']
                  }
                }
              }
            }
          },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'file_document',
            label: 'File Dokumen / Bukti',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'file_document.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }],
                view_data: 'file_document'
              }
            }
          },
          {
            id: 'status_payment',
            label: 'Status Pembayaran',
            methods: {
              list: {
                order: true,
                class: { 'not_finished_yet': 'badge badge-danger', 'finished': 'badge badge-primary' },
                transform: 'finished'
              },
              detail: {
                order: true,
                class: { 'not_finished_yet': 'badge badge-danger', 'finished': 'badge badge-primary' },
                transform: 'finished'
              },
              create: false,
              update: false,
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah Selesai', code: 'finished' }, { label: 'Belum Selesai', code: 'not_finished_yet' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



